// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import EzService from './service/ezService'
import VueI18n from 'vue-i18n'

import i18nMessage from './lib/i18n'
import VueMessages from './plugins/vueMessage/vueMessage'
import VueNotice from './plugins/vueNotice/vueNotice'

import App from './App'
import router from './router'

import VueMarkdown from 'vue-markdown'

import VButton from './components/vButton/VButton'
import VButtonGroup from './components/vButton/VButtonGroup'
import VRadio from './components/vRadio/VRadio'
import VRadioGroup from './components/vRadio/VRadioGroup'

Vue.config.productionTip = false

Vue.use(EzService, { timeout: 3000 })
Vue.use(VueI18n)
Vue.use(VueMessages, { duration: 2000 })
Vue.use(VueNotice, { reverse: false })

var commonComs = {
  'v-markdown': VueMarkdown,
  'v-button': VButton,
  'v-button-group': VButtonGroup,
  'v-radio': VRadio,
  'v-radio-group': VRadioGroup
}

for (var key in commonComs) {
  Vue.component(key, commonComs[key])
}

Vue.mixin({
  methods: {
    logTheError (error) {
      if (console && console.error) {
        console.error(error)
      }
    },
    handleError (error) {
      if (!error.ok) {
        // window.location.reload()
        console.log(error)
      } else {
        if (console && console.error) {
          console.error(error)
        }
      }
    }
  }
})

const i18n = new VueI18n({
  locale: 'zh-CN',
  silentTranslationWarn: true,
  missing: (locale, key, vm) => {
    return key
  },
  messages: i18nMessage
})

const pageTitleDom = (document.head && document.head.getElementsByTagName('title')[0]) ? (document.head && document.head.getElementsByTagName('title')[0]) : null
const setHtmlPageTitle = function (newTitle) {
  if (pageTitleDom) {
    let _newTitle = newTitle ? i18n.t(newTitle) + ' | ' + i18n.t('text.bolgName') : i18n.t('text.bolgName')
    pageTitleDom.innerText = _newTitle
  }
}

router.afterEach(route => {
  let _title = route.meta.pageTitle ? route.meta.pageTitle : ''
  setHtmlPageTitle(_title)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  template: '<App/>',
  components: { App }
})



