<template>
  <div class="content-view">
    <div class="content-list">
      <template v-for="(group, index) in postData.postList">
        <router-link class="content-list-item" v-if="typeof group === 'string'" :to="{ name: detail, params: { subName: group } }" :title="postData.postMap[group].postTitle" :key="index">
            <i class="content-list-item-icon" :class="postData.postMap[group].iconClass"></i>
            <span>{{ postData.postMap[group].postTitle }}</span>
        </router-link>
        <div v-else class="content-list-group" :key="index">
          <div class="content-list-group-title">
            {{ group.groupName }}
          </div>
          <router-link class="content-list-item" v-for="(item, index) in group.subs" :key="item" :to="{ name: detail, params: { subName: item } }" :title="postData.postMap[item].postTitle">
            <i class="content-list-item-icon" :class="postData.postMap[item].iconClass"></i>
            <span>{{ postData.postMap[item].postTitle }}</span>
          </router-link> 
        </div>
      </template>
    </div>
    <div class="content-body">
      <router-view class="content-body-inner" :post-url="postUrl" :post-timestamp="postTimestamp" :demo="demo"></router-view>
    </div>
  </div>
</template>

<script>
import postData from './assets/postList'

export default {
  name: 'componentView',
  data () {
    return {
      detail: this.$route.meta.detail,
      postData: postData
    }
  },
  created () {
    if (!this.$route.params.subName) {
      this.$router.push({
        name: this.detail,
        params: {
          subName: postData.postList[0].subs[0]
        }
      })
    }
  },
  computed: {
    postUrl () {
      let _url = ''
      try {
        _url = this.postData.postMap[this.$route.params.subName] ? this.postData.postMap[this.$route.params.subName].postUrl : ''
      } catch (e) {}
      return _url
    },
    postTimestamp () {
      let _timestamp = ''
      try {
        _timestamp = this.postData.postMap[this.$route.params.subName] ? this.postData.postMap[this.$route.params.subName].timestamp : ''
      } catch (e) {}
      return _timestamp
    },
    demo () {
      let _demo = ''
      try {
        _demo = this.postData.postMap[this.$route.params.subName] ? this.postData.postMap[this.$route.params.subName].demo : ''
      } catch (e) {}
      return _demo
    }
  },
  methods: {
  },
  components: {
  }
}
</script>

<style src="./component.less" lang="less"></style>



